@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap');
/* base styles */
* {
  margin: 0;
  font-family: 'Inconsolata', monospace;
  color: #333;
  background-color: black;
  color: #f0f0f0;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 80%;
  margin: 0 auto;
}
.navbar h1 {
  color: #E5E5E5;

}
.navbar a:hover {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #f0f0f0;
}
.navbar h1:hover{
  color: #f1356d;
}

.content {
  max-width: 800px;
  margin: 10px auto;
  padding: 20px;
}
.page-title {
  align-items: center;
  font-size: 60px;
  margin-bottom: 20px;
  color: #f0f0f0;
}
.page-title:hover {
  color: #f1356d;
}
.blog-list {
  display: grid;
  grid-template-columns: auto auto auto;
}

/* blog previews / list */

.blog-preview {
  border-radius: 0px;
  background: black;
  margin-bottom: 20px;
  padding-right: 20px;

}
.blog-preview:hover {
  box-shadow: 1px 3px 5px #f1356d;
}
.blog-info {
  text-align: left;
  margin-bottom: 20px;
}
.blog-preview h2 {
  font-size: 20px;
  color: #E5E5E5;
  margin-bottom: 8px;
}
.blog-preview a{
  text-decoration: none;
}
/* blog details page */
.blog-details h2 {
  font-size: 20px;
  color: #E5E5E5;
  margin-bottom: 10px;
}
.blog-details div {
  margin: 20px 0;
  vertical-align: middle
}
.blog-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.not-found h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.not-found p{
  margin: 20px 0;
  vertical-align: middle;
}
.not-found a{
  text-decoration: none;
}
.footer{
  padding: 40px 0 20px;
  display: flex;
  justify-content: center;
  max-width: 600px;
  margin: 0 auto;
  border-top: 1px dotted #f2f2f2; 
  bottom: 0;
  
}
